            <div class="subtitle">中国粮食的变迁史</div>
                <p class="text">我国是重要的粮食大国，从新石器时代人们就开始大规模种植粮食。但受到自然地理环境与生产技术的影响，每种粮食的生产期却不同。有的作物生命力旺盛，适应能力强，就可以在更广阔的地理区域内生长，如小麦、水稻、粟、黍等。而有的作物只能在特定的环境里繁衍，如一些热带作物等，这种作物就具有较强的地域性特点。在我国不同的历史时期，南北粮食作物都是互动的，除此之外，还有部分外来作物，比如我们今天吃的马铃薯、花生、甘薯等，就是洋货。</p>
            <div class="inner-border"></div>


            <div class="subtitle">一、史前至秦汉中国粮食结构的形成与改变</div>
                <p class="text">历史时期的粮食作物多是在史前农业的基础上继承的。目前通过对各种文化的考古发现可以了解到，中国南北作物的分布各有差异，北方的作物多以粟与黍为主，南方的作物多以水稻为主。</p>
                <br/>
                <p class="text">在北方目前已知的龙山文化、仰韶文化、马家窑文化、齐家文化都出现了粟。在黄河流域以外的地区，如西藏的卡若文化、东北的昂昂溪文化也都有粟的发现。到了仰韶文化中期，粟已经在中原地区大面积种植，这不仅满足了人们对于稳定粮食的需求，同时也为文明的发展以及人类更大聚落的形成提供了基础。而在史前社会的南方，稻是主要的粮食作物，例如在长江流域的大溪文化、良渚文化、屈家岭文化中都有大量稻的出现。黄河流域虽然也有稻的出现，但地域分布十分有限，仅仅在渭河与伊洛盆地发现稻的痕迹。而到了商周时期，原产于西亚的小麦，开始传入中国。</p>
                    
