            <div class="subtitle">二、魏晋至两宋时期原生粮食的扩大与外来作物的传入</div>
                <p class="text">从魏晋到两宋之间，中国先后经历了大分裂到统一再到分裂的局面，各民族之间频繁的交流与融合推动着物资的交流与新的粮食作物的传入。首先在传统粮食方面，小麦的种植面积在不断扩大，三国时期，在兖州与徐州就有大范围小麦种植的记录。到了唐代，河北道、河南道、淮南道（今安徽中北部与苏北地区）、河东道（今山西）都有小麦的种植。唐代宗时期甚至出现了麦税的征收，这说明小麦已经成为中国主要的粮食作物了。</p>

            <div class="inner-image">
