            <div class="subtitle">三、近代来我国粮食发展情况</div>
                <p class="text">从各个历史阶段来看，粮食发展速度也不均衡,建国初期（1949-1958年），由于在全国范围内推广土改政策，全国粮食产量连续九年丰收，增产粮食8447万吨，年均增加 938万吨；但随后“大跃进”和“文化大革命”运动造成我国粮食生产出现徘徊阶段，从1958年到1978年的二十年里我国粮食产量仅增加 1亿吨，年均增加 500万吨。1978年党的十一届三中全会，确定了我国农村联产承包责任制，广大农民的生产积性被极大地调动起来，此后，粮食生产发展速度明显加快。到1996年全国粮食总产量突破5亿吨，达到5.045亿吨，创历史最好水平。1997和1998年虽然受到较大自然灾害的影响，粮食总产量仍达到 4.9亿吨以上，只用了近20年时间就上了两个亿吨台阶，年均增加1000万吨，比前30年多增加 340万吨。我国粮食总产量稳居世界第一位。粮食人均产量1952年仅有 288公斤，1978年为319公斤，1998年达到400公斤以上。河北、内蒙古、辽宁、吉林、黑龙江、江苏、安徽、江西、山东、河南、湖北、湖南、宁夏和新疆14个省、区人均占有粮食超过400公斤。在粮食供求关系崐上已出现了总量供求基本平衡的良好局面。从粮食增产因素来看，粮食亩产水平的提高是我国粮食增产的主要因素。1949年我国粮食亩产水平只有68.6公斤，到1998年全国粮食亩产达到300 公斤，这期间粮食作物亩产增加231公斤，增长3.4倍，而这期间粮食播种面积只增长3.5%。我国粮食亩产大幅度提高除受国家宏观政策影响，农民生产积极性大幅度提高，生产投入加大以外，另一个重要原因是农业科学技术快速发展和大面积推广。建国以后，我国农业科技有了很大发展，尤其是改革开放以后，国家在农业生产上实施了丰收计划等科技措施，如水稻旱育稀植和抛秧、地膜玉米栽培、种子包衣等技术大面积的推广，促使我国农业生产科技含量增加，粮食单产水平大大提高。据有关部门反映，我国从1978年实施种植业丰收计划以来，农作物平均单产比实施前提高10-15个百分点。目前我国农业科技贡献率已达39%。</p>
            
